@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap);
.logo {
  font-size: 2.4rem;
  color: #11999e;
}

.logo-shop {
  font-size: 1.9rem;
  color: #007580;
  background: #9ddfd3;
  padding: 0.3rem 0.5rem;
  border-radius: 5px;
  opacity: 0.6;
}

.logo-shop:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  color: #9ddfd3;
  background: #007580;
}

.logo-shop:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);

}

.nav-item button {
  background: #11999e !important;
  color: #e4f9f5 !important;
}

.nav-item button:hover {
  color: #f6f6f6 !important;
}

@media only screen and (max-width: 768px) {
  .nav-item {
    text-align: center;
    margin: 5px auto;
  }
  .nav-item button {
    display: block;
    width: 120px;
  }
  .logo-shop {
    width: 100px;
  }
}

.footer {
  min-width: 100%;
  min-height: 300px;
  background: #f5f6fa;
  box-shadow:  0 -1px 2px #dcdde1 ;
  color: #353b48;
 right: 0;
 bottom: 0;
 position: relative;
}

.icons {
  border: 0.1px solid rgba(53, 59, 72,0.2);
 font-size: 1.1rem;
 padding: 0 0.4rem;
 border-radius: 5px;
 cursor: pointer;
 transition: 0.3s all ease;
}

.icons:hover {
  background: #9c88ff;
  border: 1px solid #9c88ff;
  color: aliceblue;
 
}

.list-group-item {
  background: transparent !important;
}

.list-group-item a {
  text-decoration: none;
}
.list-group-item a:hover {
  color: #9c88ff;
}


@media only screen and (max-width : 768px) {
  .etelaat {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}

body {
  margin: 0;
  min-width: 100%;
  padding: 0;
  box-sizing: border-box;
  font-family: "Vazir", sans-serif !important;
  background: #F9F7F7 !important;
}

main {
  min-height: 100vh;
}

.card {
  font-family: "Quicksand" !important;
  transition: 0.09s all ease-out !important;
  
}
.card:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}


.h5 {
  font-size: 1.12rem !important;
  font-weight: normal !important;
}

.card-img-top {
  cursor: pointer !important;
}

.form {
  max-width: 200rem !important;
}

.icon-location {
  top: 27%;
  left: 2.5%;
}


.illustrator {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 300px;
}

.popcorn {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 300px;
}

.error-image-pedar {
  width: 100%;
}

.error-image {
  max-width: 100%;
  /* min-width: 350px; */
  background-origin: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.anime-error {
  -webkit-animation: erroranimation 1.2s linear infinite alternate;
          animation: erroranimation 1.2s linear infinite alternate;
  max-width: 100%;
  background-origin: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@-webkit-keyframes erroranimation {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(-8%);
            transform: translateY(-8%);
  }
}

@keyframes erroranimation {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    -webkit-transform: translateY(-8%);
            transform: translateY(-8%);
  }
}

.input {
  border: none !important;
  outline: none !important;
  background: rgba(245, 246, 250,1.0) !important;
  width: 350px !important;
  border-radius: 7px;
  box-shadow:inset 0 0 3px rgba(113, 128, 147,0.4);
  color: rgba(53, 59, 72,1.0);
  letter-spacing: 1px;
  font-size: 18px;
}

.search-icon {
  position: absolute;
  left: 2%;
  top: 25%;
  font-size: 1.3rem;
  color: rgba(72, 126, 176,1.0);
}

.link-footer {
  cursor: pointer;
}
.link-footer:hover {
  color: #6c5ce7;
}

@media only screen and (max-width: 480px) {
  .h5 {
    font-size: 0.9rem !important;
  }
 
  .card-title {
    margin-bottom: 5px !important;
  }
  .text-muted {
    margin-bottom: 5px !important;
    font-size: 0.78rem !important;
    font-weight: 400;
  }
  .btn {
    padding: 0.20rem 0.5rem !important;
    font-size: 0.875rem !important;
    line-height: 1.5 !important;
    border-radius: 0.2rem !important;
    margin-bottom: auto !important;
  }


}



